<template>
    <div class="window monetization-channel-window"> 
        <p class="window-text" v-html="$localizeFilter('your_telegram_nickname_link')"></p>

        <div class="input-field">
            <input class="custom-input-2" :placeholder="$localizeFilter('you_in_telegram')" type="text" v-model.trim="telegramName" :class="{ invalid: v$.telegramName.$error, active: isEditing, disabled: !isEditing }"/>
            <small class="error" v-for="(error, index) of v$.telegramName.$errors" :key="index">
                {{ error.$message }}
            </small>
        </div>

         <p class="window-text" v-html="$localizeFilter('your_telegram_channel_for_our_ads')"></p>

        <div class="input-field">
            <input class="custom-input-2" :placeholder="$localizeFilter('your_channel_in_telegram')" type="text" v-model.trim="link" :class="{ invalid: v$.link.$error, active: isEditing, disabled: !isEditing }"/>
            <small class="error" v-for="(error, index) of v$.link.$errors" :key="index">
                {{ error.$message }}
            </small>
        </div>

        <div class="button-block">
            <button class="basic-button" @click="edit">{{isEditing ? (isNew ? $localizeFilter('monetize_my_traffic') : $localizeFilter('save')) : $localizeFilter('edit')}}</button>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { helpers, required } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    props: {
        idOverride: {
            type: Number,
            default: null
        },
        telegramNameOverride: {
            type: String,
            default: null
        },
        linkOverride: {
            type: String,
            default: null
        }
    },
    validations() {
        return {
            telegramName: {
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required)
            },
            link: {
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required)
            },
        };
    },
    setup() {
        return { v$: useVuelidate() };
    },
    data: () => ({
        id: null,
        telegramName: null,
        link: null,
        isEditing: false,
        isNew: true
    }),
    methods: {
        async edit() {
            if (this.isEditing) {
                await this.saveData()

            } else {
                this.isEditing = true
            }
        },
        async saveData() {
            this.v$.$touch()
            if (this.v$.$error) { return }
            try {
                if (this.isNew) {
                    const data = {telegramName: this.telegramName, link: this.link}
                    this.id = await this.$store.dispatch('addMonetizationChannel', data)
                    this.$emit('monetizationChannelAdded', this.id)
                    this.isNew = false
                    this.isEditing = false
                    this.$message(localizeFilter('your_application_sent_successful'))
                } else {
                    const data = {id: Number(this.id), telegramName: this.telegramName, link: this.link}
                    await this.$store.dispatch('editMonetizationChannelById', data)
                    this.isEditing = false
                     this.$message(localizeFilter('changes_saved'))
                }
            } catch(e) {
                this.$error(e)
            }
        }
    },
    mounted() {
        this.id = this.idOverride
        if (this.id) { this.isNew = false }
        else { this.isEditing = true }
        this.telegramName = this.telegramNameOverride
        this.link = this.linkOverride
    }
})
</script>

